body {
  color: #000;
  font-family: Roboto, sans-serif;
}

body.light-theme {
  color: #000;
}

body.dark-theme {
  color: rgba(255, 255, 255, .9);
  background-color: #121212;
}

body.dark-theme.dark-theme footer {
  background-color: inherit;
}

.body__container {
  max-width: 100%;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:before, :after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img, svg {
  height: auto;
  display: block;
}

img {
  max-width: 100%;
}

button {
  cursor: pointer;
  background-color: inherit;
  border: rgba(0, 0, 0, 0);
  margin: 0;
  padding: 0;
  font-family: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

* {
  scroll-behavior: smooth;
}

.card__colection {
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .card__colection {
    flex-wrap: wrap;
    justify-content: center;
    margin: -15px;
    padding-bottom: 50px;
    display: flex;
  }
}

.card {
  cursor: pointer;
  transition: transform .2s linear;
  transform: scale(1);
}

.card:hover, .card:focus {
  transform: scale(1.03);
}

@media screen and (max-width: 767px) {
  .card:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 768px) {
  .card {
    flex-basis: calc(50% - 30px);
    margin: 15px;
  }
}

@media screen and (min-width: 1024px) {
  .card {
    flex-basis: calc(33.3333% - 30px);
  }
}

.card img {
  width: 450px;
  border-radius: 5px;
  margin-bottom: 8px;
}

@media screen and (min-width: 768px) {
  .card img {
    width: 455px;
  }
